.header {
  padding-top: 21px;
  padding-bottom: 21px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: $backgroundAll;
  margin: 0 auto;
  &__logo {
    width: 93px;
    height: 38px;
    fill: none;
  }
  &__list {
    display: flex;
    justify-content: space-around;
  }
  &__item {
    cursor: pointer;
    transition-duration: 250ms;
    padding: 10px 15px;
    border-radius: 5px;
    margin: 0 20px;
  }
  &__item:hover {
    background-color: $backgroungSelect;
  }
  &__open-menu {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  &__arrow {
    width: 15px;
    height: 19px;
    fill: $backgroundBtn;
    transform: rotate(180deg);
  }
  &__link {
    font-size: 12px;
    font-weight: 400;
  }
  &__link:first-child {
    margin-right: 10px;
  }
  &__wellcome {
    font-size: 12px;
    font-weight: 400;
  }

  &__menu {
    display: none;
    position: absolute;
    top: 80px;
    background-color: $backgroundAll;
    z-index: 10;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    width: 160px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .show {
    display: block;
  }

  &__menu-item {
    padding: 10px 20px;
    cursor: pointer;
    transition-duration: 250ms;
    overflow: hidden;
  }
  &__menu-item:last-child {
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
  }
  &__menu-item p {
    font-size: 12px;
    color: #000;
    text-decoration: none;
  }

  &__menu-item:hover {
    background-color: $backgroungSelect;
  }
  &__wrap {
    display: flex;
    align-items: center;
  }
  &__light {
    width: 40px;
    height: 20px;
    background-color: $backgroundLights;
    border-radius: 11px;
    margin-right: 51px;
    cursor: pointer;
  }
  &__light-circle {
    width: 20px;
    height: 20px;
    background-color: $backgroundAll;
    border: 1px solid $backgroundBtn;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  &__light-icon {
    width: 15px;
    height: 15px;
  }
  &__modal-open {
    background-color: transparent;
    border: none;
    margin-left: 15px;
    cursor: pointer;
  }
  &__modal-open:hover {
    .header__user {
      fill: $backgroundLights;
    }
  }
  &__user {
    width: 20px;
    height: 20px;
    fill: $backgroundBtn;
    transition-duration: 250ms;
  }

  &__close {
    display: flex;
    background-color: transparent;
    border: none;
    margin-left: 15px;
    margin-top: 15px;
    margin-bottom: 20px;
    cursor: pointer;
  }
  &__close:hover {
    .header__close-icon {
      fill: $backgroundLights;
    }
  }
  &__close-icon {
    fill: $backgroundBtn;
    width: 15px;
    height: 15px;
    transition-duration: 250ms;
  }
  &__modal-text {
    text-align: center;
    max-width: 392px;
    padding-bottom: 22px;
    margin: 0 auto;
  }
  &__modal-ask {
    text-align: center;
    margin-bottom: 18px;
  }
  &__modal-fill {
    display: flex;
    justify-content: center;
  }
  &__modal-name {
    border: none;
    border-radius: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 80px;
    padding-left: 10px;
    max-width: 169px;
    background-color: $backgroundInput;
    margin-bottom: 18px;
  }
  &__modal-name::placeholder {
    font-size: 12px;
    font-weight: 400;
    text-align: start;
    color: $inputText;
    font-family: 'Montserrat Alternates', sans-serif;
  }
  &__modal-save {
    border: 1px solid $backgroundBtn;
    border-radius: 20px;
    color: $backgroundAll;
    background-color: $backgroundBtn;
    padding: 10px;
    font-size: 12px;
    font-weight: 400;
    cursor: pointer;
    font-family: 'Montserrat Alternates', sans-serif;
    display: block;
    margin: 0 auto;
  }
}
#header__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 576px;
  min-height: 316px;
  background-color: $backgroundAll;
  border-radius: 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  transform: translate(-50%, -50%) scale(1);
  transition: opacity 500ms cubic-bezier(0.83, -0.08, 0.32, 1) 250ms;
  background-image: url(../images/footerback.svg);
  background-repeat: no-repeat;
  background-position: center;
  z-index: 999;
  padding-bottom: 47px;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 500ms cubic-bezier(0.83, -0.08, 0.32, 1) 250ms,
    visibility 500ms cubic-bezier(0.83, -0.08, 0.32, 1) 250ms;
  opacity: 1;
  z-index: 999;
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
