html {
  scroll-behavior: smooth;
}

.container {
  width: 1090px;
  margin: 0 auto;
  padding: 0 175px;
}

body {
  box-sizing: border-box;
  background-image: url("body.55f5ea5e.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  padding: 0;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 16px;
}

*, :after, :before {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  cursor: pointer;
  color: #000;
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

.dark {
  background-image: url("blackback.e512d7c8.avif");
}

.dark .container, .dark section {
  background-color: #293366;
}

.dark h1, .dark h2, .dark h3, .dark h4, .dark h5, .dark h6, .dark a, .dark p, .dark span, .dark input::placeholder {
  color: #d9d9d9;
}

.dark .header__light-circle, .dark .border {
  background-color: #293366;
}

.dark svg {
  stroke: #d9d9d9;
}

.dark .header__logo, .dark .footer__logo-icon {
  fill: none;
}

.dark .header__user {
  fill: #d9d9d9;
}

.dark input, .dark .field {
  color: #d9d9d9;
  background-color: #0000;
  border: 1px solid #d9d9d9;
}

.dark .header__modal, .dark .modal {
  background-color: #293366;
  background-image: none;
}

.dark .football__field {
  border-color: #d9d9d9;
}

.dark .scientist__bg {
  background-color: #293366;
}

.dark .header__item:hover {
  background-color: #000;
}

.dark .header__menu {
  background-color: #293366;
}

.dark .header__menu-item p {
  color: #d9d9d9;
}

.dark .header__menu-item:hover {
  background-color: #000;
}

button {
  cursor: pointer;
}

.main-title {
  text-align: center;
  padding-bottom: 38px;
  font-size: 24px;
  font-weight: 400;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding-top: 21px;
  padding-bottom: 21px;
  display: flex;
}

.header__logo {
  fill: none;
  width: 93px;
  height: 38px;
}

.header__list {
  justify-content: space-around;
  display: flex;
}

.header__item {
  cursor: pointer;
  border-radius: 5px;
  margin: 0 20px;
  padding: 10px 15px;
  transition-duration: .25s;
}

.header__item:hover {
  background-color: #f1f1f1;
}

.header__open-menu {
  cursor: pointer;
  align-items: center;
  display: flex;
}

.header__arrow {
  fill: #000;
  width: 15px;
  height: 19px;
  transform: rotate(180deg);
}

.header__link {
  font-size: 12px;
  font-weight: 400;
}

.header__link:first-child {
  margin-right: 10px;
}

.header__wellcome {
  font-size: 12px;
  font-weight: 400;
}

.header__menu {
  z-index: 10;
  background-color: #fff;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 160px;
  display: none;
  position: absolute;
  top: 80px;
  box-shadow: 0 8px 16px #0003;
}

.header .show {
  display: block;
}

.header__menu-item {
  cursor: pointer;
  padding: 10px 20px;
  transition-duration: .25s;
  overflow: hidden;
}

.header__menu-item:last-child {
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

.header__menu-item p {
  color: #000;
  font-size: 12px;
  text-decoration: none;
}

.header__menu-item:hover {
  background-color: #f1f1f1;
}

.header__wrap {
  align-items: center;
  display: flex;
}

.header__light {
  cursor: pointer;
  background-color: #7a7a7a;
  border-radius: 11px;
  width: 40px;
  height: 20px;
  margin-right: 51px;
}

.header__light-circle {
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  display: flex;
  position: relative;
}

.header__light-icon {
  width: 15px;
  height: 15px;
}

.header__modal-open {
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin-left: 15px;
}

.header__modal-open:hover .header__user {
  fill: #7a7a7a;
}

.header__user {
  fill: #000;
  width: 20px;
  height: 20px;
  transition-duration: .25s;
}

.header__close {
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin-top: 15px;
  margin-bottom: 20px;
  margin-left: 15px;
  display: flex;
}

.header__close:hover .header__close-icon {
  fill: #7a7a7a;
}

.header__close-icon {
  fill: #000;
  width: 15px;
  height: 15px;
  transition-duration: .25s;
}

.header__modal-text {
  text-align: center;
  max-width: 392px;
  margin: 0 auto;
  padding-bottom: 22px;
}

.header__modal-ask {
  text-align: center;
  margin-bottom: 18px;
}

.header__modal-fill {
  justify-content: center;
  display: flex;
}

.header__modal-name {
  background-color: #d7d7d7;
  border: none;
  border-radius: 20px;
  max-width: 169px;
  margin-bottom: 18px;
  padding: 10px 80px 10px 10px;
}

.header__modal-name::placeholder {
  text-align: start;
  color: #7e7e7e;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.header__modal-save {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 1px solid #000;
  border-radius: 20px;
  margin: 0 auto;
  padding: 10px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
  display: block;
}

#header__modal {
  z-index: 999;
  background-color: #fff;
  background-image: url("footerback.6930e2e9.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 4px;
  min-width: 576px;
  min-height: 316px;
  padding-bottom: 47px;
  transition: opacity .5s cubic-bezier(.83, -.08, .32, 1) .25s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.backdrop {
  opacity: 1;
  z-index: 999;
  background-color: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .5s cubic-bezier(.83, -.08, .32, 1) .25s, visibility .5s cubic-bezier(.83, -.08, .32, 1) .25s;
  position: fixed;
  top: 0;
  left: 0;
}

.leap-year__section {
  background-color: #fff;
  margin: 0 auto;
  padding-top: 36px;
  padding-bottom: 36px;
}

.leap-year .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.leap-year__title {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
}

.leap-year__list {
  justify-content: space-between;
  margin-top: 36px;
  display: flex;
}

.leap-year__field {
  background-color: #000;
  border-radius: 20px;
  width: 215px;
  height: 35px;
  display: flex;
  box-shadow: 3px 3px #00000040;
}

.leap-year__input {
  color: #7e7e7e;
  background-color: #d7d7d7;
  border: 0;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 180px;
  height: 35px;
  padding: 10px;
  font-size: 12px;
  font-weight: 400;
}

.leap-year__but {
  background-color: unset;
  cursor: pointer;
  border: 0;
  width: 35px;
  padding-top: 5px;
  display: block;
}

.leap-year__result {
  align-items: center;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.guess-the-number__section {
  background-color: #fff;
  margin: 0 auto;
  padding-top: 36px;
  padding-bottom: 36px;
}

.guess-the-number .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.guess-the-number__title {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
}

.guess-the-number__list {
  justify-content: space-between;
  margin-top: 36px;
  display: flex;
}

.guess-the-number__field {
  background-color: #000;
  border-radius: 20px;
  width: 215px;
  height: 35px;
  display: flex;
  box-shadow: 3px 3px #00000040;
}

.guess-the-number__input {
  color: #7e7e7e;
  background-color: #d7d7d7;
  border: 0;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 180px;
  height: 35px;
  padding: 10px;
  font-size: 12px;
  font-weight: 400;
}

.guess-the-number__but {
  background-color: unset;
  cursor: pointer;
  border: 0;
  padding-top: 5px;
  padding-right: 5px;
  display: block;
}

.guess-the-number__result {
  align-items: center;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.three {
  background-color: #fff;
  padding-top: 36px;
  padding-bottom: 36px;
  position: relative;
}

.three .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.three__title {
  text-align: center;
  margin-bottom: 36px;
  font-family: Montserrat Alternates, sans-serif;
  font-weight: 400;
}

.three__list {
  justify-content: center;
  display: flex;
}

.three__item {
  margin-right: 38px;
}

.three__btn {
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  transition: background-color .25s ease-in-out;
  display: flex;
}

.three__btn:hover {
  background-color: #7a7a7a;
}

.three__img {
  width: 40px;
  height: 40px;
}

.three__message {
  text-align: center;
  margin-top: 36px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.three__comp-choice {
  justify-content: center;
  align-items: center;
  display: flex;
}

.three__chose {
  text-align: center;
  margin-top: 36px;
  margin-bottom: 36px;
  margin-right: 50px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.three__feedback {
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  border-radius: 20px;
  padding: 10px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.three__result {
  position: absolute;
  bottom: 70px;
  left: 800px;
}

.calculator {
  background-color: #fff;
  padding-top: 36px;
  padding-bottom: 36px;
}

.calculator .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.calculator__title {
  text-align: center;
  margin-bottom: 36px;
  font-size: 16px;
  font-weight: 400;
}

.calculator__wrap {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.calculator__firstNum, .calculator__secondNum, .calculator__result {
  color: #7e7e7e;
  background-color: #d7d7d7;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: 135px;
  padding: 10px 20px;
  display: flex;
  box-shadow: 3px 3px #00000040;
}

.calculator__firstNum::placeholder, .calculator__secondNum::placeholder, .calculator__result::placeholder {
  font-family: Montserrat Alternative, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.calculator__symbols {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  max-width: 62px;
  display: flex;
}

.calculator__symbol {
  margin-bottom: 10px;
}

.calculator__symbol-btn, .calculator__equal {
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 50%;
  width: 27px;
  height: 25px;
  box-shadow: 3px 3px #00000040;
}

.dino {
  background-color: #fff;
  margin: 0 auto;
  padding-bottom: 36px;
}

.dino .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.dino__title {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
}

.dino__container {
  background-image: url("https://static.wpsocket.com/plugin/dinosaur-game/banner-772x250.png");
  background-position: 100% 100%;
  background-repeat: repeat-x;
  background-size: contain;
  border-radius: 15px;
  width: 100%;
  height: 200px;
  margin-top: 36px;
  position: relative;
  overflow: hidden;
}

.dino__results {
  color: #fff;
  text-align: center;
  font-size: 20px;
}

.dino__btn {
  fill: #fff;
  color: #fff;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid;
  border-radius: 15px 15px 0 0;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  padding: 10px;
  font-size: 20px;
  display: none;
}

.dino__overlay {
  z-index: 50;
  background-color: #0003;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: 100%;
  padding: 25px;
  display: flex;
  position: absolute;
}

.dino__points {
  position: absolute;
  right: 0;
}

.dino__player {
  width: 50px;
  height: 60px;
  transition: all .2s;
  position: absolute;
  bottom: 15px;
  left: 100px;
}

.dino__cactus {
  width: 32px;
  height: 50px;
  transition: all .2s;
  position: absolute;
  bottom: 15px;
  right: 0;
}

.football {
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding-bottom: 36px;
  display: flex;
}

.football .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.football__title {
  color: #000;
  text-align: center;
  margin-bottom: 36px;
  font-size: 16px;
  font-weight: 400;
}

.football__field {
  background-color: #5abb58;
  background-image: url("gate-left.2e5a6c41.png"), url("gate-right.9576791b.png");
  background-position: 0, 100%;
  background-repeat: no-repeat;
  background-size: 150px;
  border: 2px solid #000;
  border-radius: 37px;
  width: 720px;
  height: 220px;
  overflow: hidden;
}

.football__img {
  width: 50px;
  height: 50px;
  transition: transform .5s;
  position: absolute;
}

.number {
  background-color: #fff;
