  margin: 0 auto;
  padding: 36px 147px;
}

.scientist__container {
  width: 1090px;
  margin: 0 auto;
  padding: 0 147px;
}

.scientist .border {
  background-color: #000;
  width: 536px;
  height: 1px;
  margin: 0 auto 36px;
  display: flex;
}

.scientist__title {
  text-align: center;
  margin-bottom: 36px;
  font-size: 16px;
  font-weight: 400;
}

.scientist__list {
