  padding: 10px 20px;
  font-size: 12px;
  font-weight: 400;
  transition: background-color .5s ease-in-out, color .5s ease-in-out;
}

.scientist__btn:hover {
  color: #000;
  background-color: #0000;
}

.scientist__btn:hover .scientist__btn-item {
  border: 1px solid #000;
}

.wrap {
  flex-wrap: wrap;
  justify-content: center;
  width: 821px;
  display: flex;
}

.wrap__item:last-child {
  margin-top: 20px;
}

.wrap__item > .scientist__btn {
  max-width: none;
}

.scientist__item .scientist__hover {
  color: #fff;
  text-align: center;
  opacity: 0;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 14px;
  transition: opacity .3s ease-in-out;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.scientist__item:hover .scientist__hover {
  opacity: 1;
}

.footer {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
